import React, { Component } from 'react';
import { Button } from 'reactstrap';
import './graph.css';

export default class Graph extends React.Component {
  constructor(props) {
    super(props);
    this.buildGraph = this.buildGraph.bind(this);
    this.state = {
      nodes: [],
      loading: true
    }
    this.toggleFullScreen = this.toggleFullScreen.bind(this);
  }
  componentWillMount() {    
    fetch('/researchers', {
      method: 'GET',
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    })
    .then(res => res.json())
    .then(nodes => {
      this.setState({
        nodes,
        loading: false
      })
    })
  }
  buildGraph() {
    var linkDistance=200;
    var w = 0.9 * window.screen.width;
    var h = 0.9 * window.screen.height;
    var node = this.node;
    var d3 = window.d3;
    var colors = d3.scale.category10();
    var bbox;
    var rx;
    var ry;
    var researchersId = []
    
    var dataset = {
      nodes: [],
      edges: []
    }
   
    const g_nodes = [];
    const g_edges = [];
    /*For each researcher set his/her node on the graph*/
    this.state.nodes.forEach(n => {
      researchersId.push(n.id)
      g_nodes.push({
        index: n.id,
        name: n.name
      });
    })
    /*For each researcher, set the edges related to that researcher*/
    this.state.nodes.forEach(n => {
      if (n.related.length > 0) {
        n.related.forEach(r => {
          if (researchersId.indexOf(n.id) !== -1 && researchersId.indexOf(r.id) !== -1) {
            g_edges.push({
              source: n.id,
              target: r.id,
              weight: r.RelatedResearcher.cs + r.RelatedResearcher.bc
            });
          }
        })
      }
    })
    var obj = {}
    g_nodes.forEach(function(d, i){
      obj[d.index] = i;
    })    
    g_edges.forEach(function(d) {
      d.source = obj[d.source];  // look up the index of source
      d.target = obj[d.target];  // look up the index of target
    })
    var svg = d3.select(node).append("svg").attr({"width":w,"height":h});

    var force = this.force = d3.layout.force()
        .nodes(g_nodes)
        .links(g_edges)
        .size([w,h])
        .linkDistance([linkDistance])
        .charge([-500])
        .theta(0.1)
        .gravity(0.05)
        .start();

    var edges = svg.selectAll("line")
      .data(g_edges)
      .enter()
      .append("line")
      .attr("id", (d, i) => "edge" + i)
      .attr("stroke-width", (d,i) => d.weight * 8)
      .style("stroke", "#fff")
      .style("pointer-events", "none");
    
    var nodes = svg.selectAll("circle")
      .data(g_nodes)
      .enter()
      .append("circle")
      .attr("r", d => 10 + (d.weight * 1.4))
      .style("fill", (d, i) => colors(i))
      .call(force.drag)
      .on("mouseover", mouseOver(.1))
      .on("mouseout", mouseOut);


    var nodelabels = svg.selectAll(".nodelabel") 
       .data(g_nodes)
       .enter()
       .append("text")
       .attr({"x": d => d.x,
              "y": d => d.y,
              "class":"nodelabel"
            })
       .text( d => d.name.split(' ')[0].toUpperCase());

    var edgepaths = svg.selectAll(".edgepath")
        .data(g_edges)
        .enter()
        .append('path')
        .attr({'d': d => 'M '+ d.source.x + ' ' + d.source.y + ' L ' + d.target.x + ' ' + d.target.y,
               'class':'edgepath',
               'fill-opacity':0,
               'stroke-opacity':0,
               'fill':'blue',
               'stroke':'red',
               'id': (d, i) => 'edgepath' + i })
        .style("pointer-events", "none");

    var edgelabels = svg.selectAll(".edgelabel")
        .data(g_edges)
        .enter()
        .append('text')
        .style("pointer-events", "none")
        .attr({'class':'edgelabel',
               'id': (d,i) => 'edgelabel' + i,
               'dx':80,
               'dy':-10,
               });

    edgelabels.append('textPath')
        .attr('xlink:href', (d,i) =>'#edgepath' + i)
        .style("pointer-events", "none")        
        .text( (d,i) => (d.weight * 100).toFixed(2) + " %");

    // build a dictionary of nodes that are linked
    var linkedByIndex = {};
    g_edges.forEach(function(d) {
        linkedByIndex[d.source.index + "," + d.target.index] = 1;
    });

    // check the dictionary to see if nodes are linked
    function isConnected(a, b) {
        return linkedByIndex[a.index + "," + b.index] || linkedByIndex[b.index + "," + a.index] || a.index == b.index;
    }
/*    svg.append('defs').append('marker')
        .attr({'id':'arrowhead',
               'viewBox':'-0 -5 10 10',
               'refX':25,
               'refY':0,
               //'markerUnits':'strokeWidth',
               'orient':'auto',
               'markerWidth':10,
               'markerHeight':10,
               'xoverflow':'visible'})
        .append('svg:path')
            .attr('d', 'M 0,-5 L 10 ,0 L 0,5')
            .attr('fill', '#ccc')
            .attr('stroke','#ccc');*/
    // fade nodes on hover
    function mouseOver(opacity) {
        return function(d) {
            // check all other nodes to see if they're connected
            // to this one. if so, keep the opacity at 1, otherwise
            // fade
            nodes.style("stroke-opacity", function(o) {
                var thisOpacity = isConnected(d, o) ? 1 : opacity;
                return thisOpacity;
            });
            nodes.style("fill-opacity", function(o) {
                var thisOpacity = isConnected(d, o) ? 1 : opacity;
                return thisOpacity;
            });
            // also style link accordingly
            edges.style("stroke-opacity", function(o) {
                return o.source === d || o.target === d ? 1 : opacity;
            });
            edgelabels.style("fill-opacity", function(o) {
                return o.source === d || o.target === d ? 1 : opacity;
            });
            nodelabels.style("fill-opacity", function(o) {
                var thisOpacity = isConnected(d, o) ? 1 : opacity;
                return thisOpacity;
            });

            /*edges.style("stroke", function(o){
                return o.source === d || o.target === d ? o.source.colour : "white";
            });*/
        };
    }

    function mouseOut() {
        nodes.style("stroke-opacity", 1);
        nodes.style("fill-opacity", 1);
        edgelabels.style("fill-opacity", 1);
        nodelabels.style("fill-opacity", 1);
        edges.style("stroke-opacity", 1);
        /*edges.style("stroke", "#ddd");*/
    }  

    force.on("tick", function(){

        edges.attr({"x1": function(d){return d.source.x;},
                    "y1": function(d){return d.source.y;},
                    "x2": function(d){return d.target.x;},
                    "y2": function(d){return d.target.y;}
        });

        nodes.attr({"cx":function(d){return d.x;},
                    "cy":function(d){return d.y;}
        });

        nodelabels.attr("x", function(d) { return d.x; }) 
                  .attr("y", function(d) { return d.y; });

        edgepaths.attr('d', function(d) { var path='M '+d.source.x+' '+d.source.y+' L '+ d.target.x +' '+d.target.y;
                                           //console.log(d)
                                           return path});       

        edgelabels.attr('transform',function(d,i){
            if (d.target.x<d.source.x){
                bbox = this.getBBox();
                rx = bbox.x+bbox.width/2;
                ry = bbox.y+bbox.height/2;
                return 'rotate(180 '+rx+' '+ry+')';
                }
            else {
                return 'rotate(0)';
                }
        });
    });
  }
	componentDidMount() {
		return !this.state.loading && this.buildGraph();
	}
  componentDidUpdate() {
    return !this.state.loading && this.buildGraph();
  }
  componentWillUnmount () {
    
  }
  toggleFullScreen() {
    console.log(this.graph)
    if (!document.mozFullScreen && !document.webkitFullScreen) {
      if (this.graph.mozRequestFullScreen) {
        this.graph.mozRequestFullScreen();
      } else {
        this.graph.webkitRequestFullScreen(Element.ALLOW_KEYBOARD_INPUT);
      }
    } else {
      if (document.mozCancelFullScreen) {
        document.mozCancelFullScreen();
      } else {
        document.webkitCancelFullScreen();
      }
    }
  }
	render() {
		return this.state.loading ? <p>Carregando...</p> : 
    (
      <div ref={node => this.graph = node} id="graph-bg">
        <Button color="primary" onClick={this.toggleFullScreen} style={{position: 'absolute', top: 0, lef:0}}>Tela cheia</Button>
        <svg ref={node => this.node = node} width={0.9 * window.screen.width} height={window.screen.height - 56}>
        </svg>
      </div>
    )
	}
}


